.MenuList {
  max-width: 900px; margin:50px auto;
  h1 { text-align: center; }
  ul { background: #ccc; 
    li {
      float:left; width:25%;
      text-align: center; border:1px solid #fff;
      img { width:100%; opacity: 0.7; transition: all 0.5s;
        &:hover { opacity: 1; }
      }
      span { display: block; margin: 10px 0; }
    }
  }
}


